.text {
  display: flex;
  padding: 1rem 0.5rem;
  background: #22262c;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.selected {
  background: var(--orange);
}

.profile {
  display: flex;
  flex: 0.7;
  color: white;
  flex-direction: column;
}

.profile a {
  font-size: 12px;
  color: white;
  text-decoration: none;
  display: flex;
  align-items: center;
}

.profile a img {
  width: 13px;
}

.profile p {
  font-size: 14px;
  color: white;
  font-weight: bold;
  margin-bottom: 0.5rem;
}
.photo {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 1rem;
}

.photo img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.marginBottom {
  border-bottom: 1px solid darkgray;
  /* margin-bottom: 0.5rem; */
  align-items: center;
}

.chatBubble {
  display: flex;
  max-width: 40%;
  padding: 0.5rem;
  background: var(--black);
  color: white;
  border-radius: 10px;
  overflow-wrap: break-word;
}
